  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.typed-element .typed-cursor {
    opacity: 1;
    -webkit-animation: typedjsBlink 0.7s infinite;
            animation: typedjsBlink 0.7s infinite;
}
@-webkit-keyframes typedjsBlink {
50% {
    opacity: 0.0;
}
}
@keyframes typedjsBlink {
50% {
    opacity: 0.0;
}
}
